.contantwrapper{
    width: 100vw;
    min-height: 90vh;
    position: relative;
}
.contantcon{
    width: 100vw;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
}
.contanttext{
    text-align: center;
    color: #818486;
    width: 55%;
    font-size: 1.3rem;
    
}
.email{
    color: #86C232;
    font-size: 1.4rem;
    text-decoration: none;
    transition: 0.5s;
}
.email::selection{
    color:white;
    background-color: #86C232;
}
.email:hover{
    letter-spacing: 0.1rem;
}
.socialtext{
    width: 100%;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    line-height: normal;
}
#sl{
    transform: scale(1.2);
}
.footer{
    color: #818486;
    font-size: 0.9rem;
    text-align: right;
    position: absolute; 
    right: 0;  
    bottom: 0;
    margin-right: 15px;
    overflow: hidden;
}
@media screen and (max-width:900px) {
    .contanttext{
        width: 95%;
    }
    
}