.calcwrap{
    width: 100vw;
    margin-top: 10px;
}
.calccont{
    display: grid;
    height: 100%;
    min-height: 75vh;
    grid-template-columns: 20% 80%;
    position: relative;
    top: -25px;

    
}
.calcp{
    grid-column-start: 2;
    grid-row-start: 1;
    background:url(../images/calc.png);
    background-size: cover;
    border-radius: 15px 0 0 15px;
    -webkit-box-shadow: -2px 0px 15px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: -2px 0px 15px -2px rgba(0,0,0,0.75);
    box-shadow: -2px 0px 15px -2px rgba(0,0,0,0.75);
    background-position: center;
}
.calcinfo{
    grid-column-start: 1;
    width: 97%;
    margin-left: 5px;
}
.calcinfo > p{
    color:#b4b9bb;;
    font-weight: 300;
}
.calcinfo > a {
    text-decoration: none;
    color: #86C232;
    transition: 0.5s;
}
.calcinfo > a:hover{ 
    letter-spacing: 0.1rem;
}
@media screen and (max-width: 820px){
    .calccont{
        grid-template-columns: 1fr;
        grid-auto-rows: auto;
    }
    .calcinfo{
        grid-column-start: 1;
        grid-row-start: 2;
    }
    .calcp{
        grid-column-start: 1;
        height: 25vh;
        width: 99%;
        border-radius: 0;
    }
}
