.wrapabout{
    width: 100vw;
    min-height:100vh;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(2, minmax(320px, 1fr));
    
}
.abouts{
    grid-column-start: 2;
    position: relative;
    min-height: 50vh;
    right: 20px;
    align-self: center;
    
}
.header{
    font-size: 60px;
    color: #86C232;
    font-weight: 300;
}
.abouttext{
    color: #818486;
    font-size: 1.5rem;
    text-align: left;
    line-height: 32px;
}
.skills{
    grid-row-start: 1;
    grid-row-end: 1;
    grid-column-start: 1;
    grid-column-end: 1;
    margin-left: 20px;
    min-height: 50vh;
    justify-self: center;
}
.skillstext{
    color: #818486;
    font-size: 1.5rem;
}
@media screen and (max-width: 700px) {
    .abouts{
        grid-row-start: 2;
        grid-column-start: 1;
        right: 0;
    }
    .abouts, .skills{
        margin-left: 5px;
        bottom: 0;
        justify-self:auto;
    }
    .skills{
        height: fit-content;
    }
}
