.workwrapper{
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-flow: column wrap;
    text-align: center;
}

.headerw{
    font-size: 60px;
    color: #86C232;
    font-weight: 300;

}
.workcont{
    display: flex;
    flex-flow: column wrap;
}

.projectsw, .contributed{
    color:#818486;
    font-size: 1.5rem;
}
.projectcard{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
}
.card{
    display: flex;
    flex-flow: row wrap;
    height: 300px;
    width: 250px;
    border-radius: 10px;
    justify-content: center;
    border: 1px solid #86C232;
    margin-bottom: 15px;
}
.bottom{
    color: #b3b8bb;
    font-size: 2rem;
    align-self: center;
    width: 100%;
    font-weight: 300;
    border-radius: 0 0 10px 10px;
}
.card{
    cursor: pointer;
}
#card1::before, #card2::before, #card3::before, #card4::before, #card5::before, #card6::before, #card7::before{
    content:'';
    position:absolute;
    height: 300px;
    width: 250px;
    background-size: cover;
    border-radius: 10px;
    opacity:0;
    -webkit-transition: opacity .7s ease-out;
    transition: opacity .7s ease-out;
}
#card1:hover::before, #card2:hover::before, #card3:hover::before, #card4:hover::before, #card5:hover::before,#card6:hover::before,#card7:hover::before{
    opacity:1;
}
#card1::before{
    background: url(../images/weather.png);
    background-size: cover;
}
#card2:hover::before{
    background: url(../images/calc.png);
    background-size: cover;
}
#card3:hover::before{
    background: url(../images/robofriends.png);
    background-size: cover;
}
#card4:hover::before{
    background: url(../images/portfolio.png);
    background-size: cover;
}
#card5:hover::before{
    background: url(../images/tictac.png);
    background-size: cover;
    background-position: center;
}
#card6:hover::before{
    background: url(../images/concept.png);
    background-size: cover;
}
#card7:hover::before{
    background: url(../images/shop.png);
    background-size: cover;
}
.card:hover .bottom{
display: none;
}

