.portwrap{
    width: 100vw;
    margin-top: 10px;
}
.portcont{
    display: grid;
    height: 100%;
    min-height: 75vh;
    grid-template-columns: 20% 80%;
    position: relative;
    top: -25px;
    
}
.portp{
    grid-column-start: 2;
    grid-row-start: 1;
    background:url(../images/portfolio.png);
    background-size: cover;
    border-radius: 15px 0 0 15px;
    -webkit-box-shadow: -2px 0px 15px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: -2px 0px 15px -2px rgba(0,0,0,0.75);
    box-shadow: -2px 0px 15px -2px rgba(0,0,0,0.75);
    
}
.portinfo{
    grid-column-start: 1;
    width: 97%;
    margin-left: 5px;
}
.portinfo > p{
    color:#b4b9bb;;
    font-weight: 300;
}
@media screen and (max-width: 820px){
    .portcont{
        grid-template-columns: 1fr;
        grid-auto-rows: auto;
    }
    .portinfo{
        grid-column-start: 1;
        grid-row-start: 2;
    }
    .portp{
        grid-column-start: 1;
        height: 25vh;
        width: 99%;
        border-radius: 0;
    }
}
