.wraphome{
    min-height: 100vh;
    width: 98vw;
    margin: 0 auto;
    display: flex;  
    flex-flow: column wrap;
    overflow: hidden;
    justify-content: center;

}
.bgText{
    color: #474b4f69;
    text-align: justify;    
    font-size: 15.5vw;
    line-height: 0;
    position: absolute;
    font-weight: 500;
    -webkit-user-select: none;        
    -moz-user-select: none;
    -ms-user-select: none; 
    user-select: none; 
    overflow: hidden;
}
.frontText{
    position: absolute;
    color: #86C232;
    font-size: 10vw;
    -webkit-user-select: none;        
    -moz-user-select: none;
    -ms-user-select: none; 
    user-select: none; 
    overflow: hidden;
}
.social{
    display: flex;
    flex-flow: row wrap;
    position: relative;
    margin-top: auto;
    margin-bottom: 50px;
    margin-left: 10px;
    align-items: center;
}
.git, .linkedin{
    width: 2rem;
    height: 2rem;
    margin-right: 6px;
    position: relative;
    fill: #6B6E70;
    cursor: pointer;
}
.git:hover, .linkedin:hover{
    transition: 1s;
    fill:#86C232;
}
.scrolld{
    width: 100%;
    height: 40px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    position: relative;
    bottom: 10px;
    cursor: pointer;
}
.scrolls{
    fill: #6B6E70;
    border-radius: 50%;
    padding: 5px;
    transform: scale(1.2);
}
.scrolls:hover{
    transition: 1s;
    background-color: #86C232;
    fill: white;
}