.navwrap{
    display: flex;
    flex-flow: row nowrap;
    color: white;
    justify-content: space-between;
    align-items: center;
    left: 0;
    top: 0;
    margin: 0 auto;
    width: 100vw;
    z-index: 10;
    position: fixed;
    background-color: #222629;
}
.brandc{
    z-index: 10;
}
.brand{
    font-size: 2rem;
    margin-top: 20px;
    height: 90%;
    width: 90%;
    margin-left: 15px;
    cursor: pointer;
}
.links{
    display: flex;
    flex-flow: column wrap;
    margin-right: 15px;
}

.bar{
    margin-top: 20px;
    cursor: pointer;
    display: flex;
    flex-flow: column wrap-reverse;
    z-index: 10;
    background: transparent;
    border: none;
}
#bar1,#bar2,#bar3{
    background-color:  white;
    border-radius: 5px;
    height: 3px;
    margin-bottom:0.5rem ;
}
#bar1{
    width: 2rem;
}
#bar2{
    width: 1.4rem;
}
#bar3{
    height: 2.5px;
    width: 2rem;
}
.nav-link{
    text-decoration: none;
    list-style-type: none;
    cursor: pointer;
    padding-top: 5px;
    padding-bottom: 5px;
}
.nav-link:hover{
    background-color: #141618;
    transition: 0.4s;   
}
.nav-links{
    display: none;
    padding: 0;
    flex-flow: column wrap;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
    height: 50vh;
    margin: 55px auto;
    text-align: center;
    font-size: 2rem;
    font-weight: 300;
    background-color: #222629;
}

