.robowrap{
    width: 100vw;
    margin-top: 10px;
    overflow-x: hidden;
}
.robocont{
    display: grid;
    width: 100%;
    min-height: 75vh;
    grid-template-columns: 80% 20%;
    position: relative;
    top: -25px;
}
.robop{
    grid-column-start: 1;
    background:url(../images/robofriends.png);
    background-size: cover;
    border-radius: 0 15px 15px 0;
    -webkit-box-shadow: 2px 0px 15px -2px rgba(0,0,0,0.75);
-moz-box-shadow: 2px 0px 15px -2px rgba(0,0,0,0.75);
box-shadow: 2px 0px 15px -2px rgba(0,0,0,0.75);
}
.roboinfo{
    grid-column-start: 2;
    width: 97%;
    margin-left: 5px;
}
.roboinfo > p{
    color: #b4b9bb;
    font-weight: 300;
}
.roboinfo > a {
    text-decoration: none;
    color: #86C232;
    transition: 0.5s;
}
.roboinfo > a:hover{ 
    letter-spacing: 0.1rem;
}
@media screen and (max-width: 820px){
    .robocont{
        grid-template-columns: 1fr;
        grid-auto-rows: auto;
    }
    .roboinfo{
        grid-column-start: 1;
        grid-row-start: 2;
    }
    .robop{
        height: 25vh;
        width: 99%;
        border-radius: 0;
    }
}

